\">\r\n                        <img\r\n                            src=\"/assets/vendors/dashboard/ven-logo-upload.png\"\r\n                            alt=\"\"\r\n                        />\r\n                        <span>Add</span>\r\n                        <input\r\n                            type=\"file\"\r\n                            name=\"vendor-logo\"\r\n                            id=\"vendor-logo\"\r\n                            hidden\r\n                        />\r\n                    </label>\r\n                </div>\r\n            </div>\r\n            <div :class=\"[loader ? 'blur row about' : 'row about']\">\r\n                <div class=\"btn-container\">\r\n                    <button\r\n                        type=\"button\"\r\n                        class=\"tab-btn active\"\r\n                        data-id=\"vendor-details\"\r\n                        title=\"Accomodation Details\"\r\n                        @click=\"toggleForm($event, true)\"\r\n                    ></button>\r\n                    <button\r\n                        type=\"button\"\r\n                        class=\"tab-btn\"\r\n                        data-id=\"vendor-add-room\"\r\n                        title=\"Room Details\"\r\n                        @click=\"\r\n                            toggleForm($event, true);\r\n                            showRoomPics();\r\n                        \"\r\n                    ></button>\r\n                    <button\r\n                        type=\"button\"\r\n                        class=\"tab-btn\"\r\n                        data-id=\"vendor-about\"\r\n                        title=\"Gallery\"\r\n                        @click=\"toggleForm($event, true)\"\r\n                    ></button>\r\n                    <button\r\n                        type=\"button\"\r\n                        class=\"tab-btn\"\r\n                        data-id=\"vendor-add-space\"\r\n                        title=\"Amenities\"\r\n                        @click=\"toggleForm($event, true)\"\r\n                    ></button>\r\n                </div>\r\n                <div class=\"about-content forms\">\r\n                    <form\r\n                        @submit.prevent=\"submitForm1($event)\"\r\n                        data-id=\"vendor-add-room\"\r\n                        class=\"content active\"\r\n                        id=\"vendor-details\"\r\n                    >\r\n                        <h1>Accomodation details</h1>\r\n                        <div\r\n                            v-if=\"formEdit.form1\"\r\n                            style=\"display: flex; justify-content: flex-end\"\r\n                            class=\"editbtn\"\r\n                        >\r\n                            <button\r\n                                class=\"shake\"\r\n                                @click=\"enableFeilds($event)\"\r\n                                style=\"\r\n                                    outline: none;\r\n                                    border: none;\r\n                                    background-color: #461952;\r\n                                    color: white;\r\n                                    padding: 4px 20px;\r\n                                    border-radius: 10px;\r\n                                    margin-bottom: 10px;\r\n                                    margin-right: 20px;\r\n                                \"\r\n                                type=\"button\"\r\n                            >\r\n                                Edit\r\n                            </button>\r\n                        </div>\r\n                        <div class=\"vendor-details-form-fields\">\r\n                            <label for=\"accName\">\r\n                                Name\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"vendorFirstName\"\r\n                                    id=\"vendorFirstName\"\r\n                                    placeholder=\"Enter your first name\"\r\n                                    v-model=\"addAccomodation.name\"\r\n                                    required\r\n                                />\r\n                            </label>\r\n                            <label for=\"accPhone1\">\r\n                                Phone Number - 1\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"accPhone1\"\r\n                                    id=\"accPhone1\"\r\n                                    placeholder=\"Enter accomodation contact number\"\r\n                                    v-model=\"addAccomodation.number_1\"\r\n                                    required\r\n                                />\r\n                            </label>\r\n                            <label for=\"accPhone2\">\r\n                                Phone Number - 2\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"accPhone2\"\r\n                                    id=\"accPhone2\"\r\n                                    placeholder=\"Enter accomodation contact number\"\r\n                                    v-model=\"addAccomodation.number_2\"\r\n                                    required\r\n                                />\r\n                            </label>\r\n                        </div>\r\n                        <div class=\"vendor-address-form-fields\">\r\n                            <label for=\"accType\">\r\n                                Accomodation type\r\n                                <select\r\n                                    v-model=\"addAccomodation.type\"\r\n                                    name=\"accType\"\r\n                                    id=\"accType\"\r\n                                    required\r\n                                >\r\n                                    <option hidden disabled selected>\r\n                                        Select vendor’s category\r\n                                    </option>\r\n                                    <option\r\n                                        v-for=\"item in subCategories\"\r\n                                        :key=\"item.id\"\r\n                                        :value=\"item.id\"\r\n                                    >\r\n                                        {{ item.name }}\r\n                                    </option>\r\n                                </select>\r\n                            </label>\r\n                            <label for=\"accTypeStar\">\r\n                                Accomodation Stars\r\n                                <select\r\n                                    v-model=\"addAccomodation.stars\"\r\n                                    name=\"accType\"\r\n                                    id=\"accType\"\r\n                                    required\r\n                                >\r\n                                    <option hidden disabled selected>\r\n                                        Select vendor’s category\r\n                                    </option>\r\n                                    <option value=\"1\">1</option>\r\n                                    <option value=\"2\">2</option>\r\n                                    <option value=\"3\">3</option>\r\n                                    <option value=\"4\">4</option>\r\n                                    <option value=\"5\">5</option>\r\n                                </select>\r\n                            </label>\r\n                            <label for=\"accAddress\">\r\n                                Address Line 1\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"accAddress\"\r\n                                    id=\"accAddress\"\r\n                                    placeholder=\"Enter address\"\r\n                                    v-model=\"addAccomodation.address1\"\r\n                                    required\r\n                                />\r\n                            </label>\r\n                            <label for=\"accAddress1\">\r\n                                Address Line 2\r\n                                <input\r\n                                    type=\"text\"\r\n                                    name=\"accAddress1\"\r\n                                    id=\"accAddress1\"\r\n                                    placeholder=\"Enter address\"\r\n                                    v-model=\"addAccomodation.address2\"\r\n                                    required\r\n                                />\r\n                            </label>\r\n                            <label for=\"accstate\">\r\n                                State\r\n                                <select\r\n                                    v-model=\"addAccomodation.state\"\r\n                                    name=\"accstate\"\r\n                                    id=\"accstate\"\r\n                                    @change=\"getDistricts($event)\"\r\n                                    required\r\n                                >\r\n                                    <option hidden disabled selected>\r\n                                        Select vendor’s category\r\n                                    </option>\r\n                                    <option\r\n                                        v-for=\"(item, index) in states\"\r\n                                        :key=\"index\"\r\n                                        :value=\"item\"\r\n                                    >\r\n                                        {{ item }}\r\n                                    </opti